import { SchemaService } from '@jsonforms/editor';

async function getSchemaFromPeregrine(searchPath: string) {
  console.log(`Search Path: ${searchPath}`);
  let obj = parseQueryString(searchPath);
  let siteName = '';
  let objectName = '';
  if (obj) {
    Object.entries(obj).forEach(([key, value]) => {
      if (key === 'sitename') {
        siteName = value;
      }
      if (key === 'objectname') {
        objectName = value;
      }
    });
  }
  if (objectName !== '' && siteName !== '') {
    let path = `/content/${siteName}/object-definitions/${objectName}/schema.json`;
    const response = await fetch(path);
    return response.json();
  } else {
    return '{}';
  }
}

function getUISchemaFromPeregrine() {
  return undefined;
}

function parseQueryString(searchPath: string): object | undefined {
  if (searchPath && searchPath.startsWith('?')) {
    searchPath = searchPath.substring(1);
    return JSON.parse(
      '{"' + searchPath.replace(/&/g, '","').replace(/=/g, '":"') + '"}',
      function (key, value) {
        return key === '' ? value : decodeURIComponent(value);
      }
    );
  } else {
    return undefined;
  }
}
export class PeregrineSchemaService implements SchemaService {
  private searchPath: string;

  constructor(searchPath: string) {
    this.searchPath = searchPath;
  }

  getSchema = async () => getSchemaFromPeregrine(this.searchPath);
  getUiSchema = async () => getUISchemaFromPeregrine();
}
